//angular, ionic
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

//Cordova plugins
import { Vibration } from '@ionic-native/vibration';
import { FCM } from '@ionic-native/fcm';
import { AppUpdate } from '@ionic-native/app-update';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { NFC, Ndef } from '@ionic-native/nfc';
import { Camera } from '@ionic-native/camera';
import { Keyboard } from '@ionic-native/keyboard';


//My Pages
import { SMDRF } from './app.component';
import { _00_Login } from '../pages/_00_Login/Login';
import { LittleCalculatorPage } from '../pages/_ZZ_CommonLib/LittleCalculator/LittleCalculator';
import { ListTablePage } from '../pages/_ZZ_CommonLib/ListTable/ListTable';
import { PaperDetailPage } from '../pages/_ZZ_CommonLib/PaperDetail/PaperDetail';
import { myCAMERAPage } from '../pages/_ZZ_CommonLib/myCAMERA/myCAMERA';
import { http_services } from '../pages/_ZZ_CommonLib/http_services';

@NgModule({
    declarations: [
        SMDRF,

        //Pages
        _00_Login,
        LittleCalculatorPage,
        ListTablePage,
        PaperDetailPage,
        myCAMERAPage,
        //Pipes
    ],
    imports: [
        BrowserModule,
        HttpModule,
        IonicModule.forRoot(SMDRF, {
            activator: 'ripple',
            backButtonText: '返回',
            mode: 'md',
            iconMode: 'md',
            pageTransition: 'md-transition',
            tabsPlacement: 'bottom'
        }, 
{
  links: [
    { loadChildren: '../pages/_01_Zone/Zone.module.ngfactory#AppModuleNgFactory', name: '_01_Zone', segment: '_01_Zone', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_02_Menu/Menu.module.ngfactory#AppModuleNgFactory', name: '_02_Menu', segment: '_02_Menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_11_CheckIn/CheckIn.module.ngfactory#AppModuleNgFactory', name: '_11_CheckIn', segment: '_11_CheckIn', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_12_ItemRcv/1_CarNo/CarNo.module.ngfactory#AppModuleNgFactory', name: '_121_CarNo', segment: '_121_CarNo', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_12_ItemRcv/2_PaperNo/PaperNo.module.ngfactory#AppModuleNgFactory', name: '_122_PaperNo', segment: '_122_PaperNo', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_12_ItemRcv/3_ItemCode/ItemCode.module.ngfactory#AppModuleNgFactory', name: '_123_ItemCode', segment: '_123_ItemCode', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_12_ItemRcv/4_ItemRcv/ItemRcv.module.ngfactory#AppModuleNgFactory', name: '_124_ItemRcv', segment: '_124_ItemRcv', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_20_Menu/Menu.module.ngfactory#AppModuleNgFactory', name: '_20_Menu', segment: '_20_Menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_21_BasketList/BasketList.module.ngfactory#AppModuleNgFactory', name: '_21_BasketList', segment: '_21_BasketList', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_23_PrintLogisticLabel/PrintLogisticLabel.module.ngfactory#AppModuleNgFactory', name: '_23_PrintLogisticLabel', segment: '_23_PrintLogisticLabel', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_22_PrintPickingLabel/PrintPickingLabel.module.ngfactory#AppModuleNgFactory', name: '_22_PrintPickingLabel', segment: '_22_PrintPickingLabel', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_24_PhotoGallery/PhotoGallery.module.ngfactory#AppModuleNgFactory', name: '_24_PhotoGallery', segment: '_24_PhotoGallery', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/_99_TEST/TEST.module.ngfactory#AppModuleNgFactory', name: '_99_TEST', segment: '_99_TEST', priority: 'low', defaultHistory: [] }
  ]
})
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        SMDRF,
        _00_Login,
        LittleCalculatorPage,
        ListTablePage,
        PaperDetailPage,
        myCAMERAPage,
    ],
    providers: [
        StatusBar,
        SplashScreen,
        http_services,
        Vibration,
        FCM,
        AppUpdate,
        NFC, Ndef,
        Camera,
        Keyboard,
        { provide: ErrorHandler, useClass: IonicErrorHandler }
    ]
})
export class AppModule { }



